.wrapper {
    display: grid;
    min-height: 100vh;
    grid-template: 60px auto 1fr auto/ 1fr;
    width: 100%;
    background-color: white;
    color: black;
}
.wrapper-black {
    display: grid;
    min-height: 100vh;
    grid-template: 60px auto 1fr auto/ 1fr;
    width: 100%;
    background-color: black;
    color: white;
}

.content {
    max-width: 1200px;
    margin: 0px auto;
}