.menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: #3CC3B4;
  padding-bottom: 15px;
  a {
    font-size: 18px;
    font-weight: 500;
    color: #fff;
    text-decoration: none;

    &:not(:last-child) {
      padding: 0px 50px 0px 0px;
    }

    &:hover {
      text-decoration: underline;
    }

    &.active {
      color: gold;
    }
  }
}
